<template>
  <div>
    <div class="actionSheet">
      <van-action-sheet v-model="sheetTradeShow" overlay class="sheet-box sheet-trade" round  @cancel="noShowSheet"  @click-overlay="noShowSheet">
        <div class="sheet-content ">
          <div class="title">
            <div class="name">
              <div :class="$i18n.locale === 'en'?'name-en':''">{{$i18n.locale === 'en'?nameEN:name}}</div>
              <div style="color: #808080">{{instrumentid}}</div>
            </div>
            <div class="close">
              <div :style="{color:add<0?$t('fall'):$t('rise')}">{{newClose}}</div>
              <div :style="{color:add<0?$t('fall'):$t('rise')}"><span v-if="add > 0">+</span>{{add}} {{diffPriceRate}}</div>
            </div>
            <div class="BAPrice">
              <div>{{$t('Buy')}}：<span style="color:#FD4F50">{{bidPrice}}</span></div>
              <div>{{$t('Sell')}}：<span style="color: #0CAD6B">{{askPrice}}</span></div>
            </div>
          </div>
          <div class="addO-but-box" >
            <div :class="$i18n.locale === 'en'?'en-addO-but':''" class="addO-but" @click="quickOrder(index)" v-for="(item,index) in butList" :style="{'background-color':butTab === index ? 'rgba(16,136,255,0.1)':'','color':butTab === index ? '#1988FF':''}" :key="item.id">{{item.title}}</div>
          </div>
          <div class="number">
            <div :class="$i18n.locale === 'en'?'en-number':''">{{$t('Number')}}</div>：<van-stepper :class="$i18n.locale === 'en'?'en-stepper':''"   v-model="stepperOne"/>
          </div>
          <div class="number">
            <div :class="$i18n.locale === 'en'?'en-number':''">{{$t('Price')}}</div>：<van-stepper :class="$i18n.locale === 'en'?'en-stepper':''" :step="tick" :min="tick"  v-model="stepperTwo" :disabled="stopNum" :disable-input="stopNum" :decimal-length="digits"/>
          </div>
          <div class="PL-but">
            <div :style="{backgroundColor:$t('fall')}" class="sheet-trade-but" @click="closePosition">{{$t('sell')}}</div>
            <div :style="{backgroundColor:$t('rise')}" class="sheet-trade-but" @click="sellOut">{{$t('buy')}}</div>
          </div>
        </div>
      </van-action-sheet>
    </div>
    <div class="dialog">
      <div class="sellOut">
        <van-dialog
          closeOnPopstate
          v-model="showClosePosition"
          show-cancel-button
          :cancel-button-text="$t('cancel')"
          cancelButtonColor="#8C8B8B" confirmButtonColor="#1988FF"
          show-confirm-button
          :confirm-button-text="$t('Confirm')"
          @confirm="ConfirmClosing"
        >
          <div slot="title" class="dia-title">{{$t('OrderInform')}}</div>
          <div slot="default" class="dia-content">
            <div>
              <div>{{$t('Name')}}：</div>
              <div class="dia-content-title">{{$i18n.locale === 'en'?nameEN:name}}</div>
            </div>
            <div>
              <div>{{$t('code')}}：</div>
              <div class="dia-content-title1">{{instrumentid}}</div>
            </div>
            <div v-if="FixedPriceOff">
              <div>{{$t('marketPrice')}}：</div>
              <div :style="{color:add<0?'#25B100':'#FE0000'}">{{newClose}}</div>
            </div>
            <div v-else>
              <div>{{$t('FixedPrice')}}：</div>
              <div>{{stepperTwo}}</div>
            </div>
            <div>
              <div>{{$t('Number')}}：</div>
              <div>{{stepperOne}}</div>
            </div>
            <div>
              <div>{{$t('direction')}}：</div>
              <div><span style="color: #FE0000">{{$t('buy')}}</span></div>
            </div>
            <div>
              <div>{{$t('margin')}}</div>
              <div>{{bondClose}}</div>
            </div>
            <div>
              <div>{{$t('ServiceCharge')}}：</div>
              <div>{{serviceChargeClose}}</div>
            </div>
            <div>
              <div>{{$t('balance')}}：</div>
              <div>{{balance}}</div>
            </div>
          </div>
<!--          <div slot="default" class="dia-content">-->
<!--            <div style="width: 60%;" class="dia-content-title" >{{name}}</div>-->
<!--            <div style="width: 40%;" v-if="FixedPriceOff">市价：{{newClose}}</div>-->
<!--            <div style="width: 40%;" v-else>限价：{{stepperTwo}}</div>-->
<!--            <div>数量：{{stepperOne}}</div>-->
<!--            <div>方向：<span style="color: #FE0000">买</span></div>-->
<!--            <div>保证金：{{bondClose}}</div>-->
<!--            <div>手续费：{{serviceChargeClose}}</div>-->
<!--&lt;!&ndash;            <div></div>&ndash;&gt;-->
<!--            <div>账号余额：{{balance}}</div>-->
<!--          </div>-->
        </van-dialog>
      </div>
      <div class="sellOut">
        <van-dialog
          closeOnPopstate
          v-model="showSellOut"
          show-cancel-button
          :cancel-button-text="$t('cancel')"
          cancelButtonColor="#8C8B8B" confirmButtonColor="#1988FF"
          :confirm-button-text="$t('Confirm')"
          @confirm="CancelClosing"
        >
          <div slot="title" class="dia-title">{{$t('OrderInform')}}</div>
          <div slot="default" class="dia-content">
            <div>
              <div>{{$t('Name')}}：</div>
              <div class="dia-content-title">{{$i18n.locale === 'en'?nameEN:name}}</div>
            </div>
            <div>
              <div>{{$t('code')}}：</div>
              <div class="dia-content-title1">{{instrumentid}}</div>
            </div>
            <div v-if="FixedPriceOff">
              <div>{{$t('marketPrice')}}：</div>
              <div :style="{color:add<0?'#25B100':'#FE0000'}">{{newClose}}</div>
            </div>
            <div v-else>
              <div>{{$t('FixedPrice')}}：</div>
              <div>{{stepperTwo}}</div>
            </div>
            <div>
              <div>{{$t('Number')}}：</div>
              <div>{{stepperOne}}</div>
            </div>
            <div>
              <div>{{$t('direction')}}：</div>
              <div><span style="color: #25B100">{{$t('sell')}}</span></div>
            </div>
            <div>
              <div>{{$t('margin')}}</div>
              <div>{{bondClose}}</div>
            </div>
            <div>
              <div>{{$t('ServiceCharge')}}：</div>
              <div>{{serviceChargeClose}}</div>
            </div>
            <div>
              <div>{{$t('balance')}}：</div>
              <div>{{balance}}</div>
            </div>
          </div>
        </van-dialog>
      </div>
    </div>
  </div>
</template>

<script>
  import { Toast } from 'vant';
  export default {
    name: "addOrder",
    data(){
      return{
        butTab:0,
        butList:[
          {
            id:0,
            title:this.$t('marketPrice')
          },
          {
            id:1,
            title:this.$t('FixedPrice')
          }
        ],
        balance:0,
        stopNum:true,
        discUrl:'',
        sheetTradeShow:'',
        showSellOut:false,
        name:'',
        stepperOne:1,
        stepperTwo:'',
        // 传给父组件的值
        instrumentid:'',
        priceType:1,
        // 买价
        bidPrice:0 ,
        // 卖价
        askPrice:0,
        // 用户id
        // bidPriceVal:this.bidPrice
        // 弹出框-平仓显示
        showClosePosition: false,
        // 平仓
        newClose:0,
        averageClose:0,
        bondClose:0,
        directionClose:0,
        serviceChargeClose:0,
        // 查询余额计时器
        balanceTime:null,
        FixedPriceOff:true,
        tick:0,
        digits:0,
        tickCopyData:''
      }
    },
    props:{
      appTick:{
        type:Array,
      },
      tradeTab:{
        type:Number
      }
    },
    watch:{
      appTick(newVal){
        if(JSON.parse(localStorage.tickCopy) !== ''){
          this.tickCopyData = JSON.parse(localStorage.tickCopy)
        }
        if (this.tickCopyData[this.instrumentid] !== undefined) {
          let data = this.tickCopyData[this.instrumentid]
          this.newClose = (data.price).toFixed( this.digits)
          this.bidPrice = (data.bidprice).toFixed( this.digits)
          this.askPrice = (data.askprice).toFixed( this.digits)
        }
      },
    },
    created(){
      let routeData = this.$route.params
      this.name = routeData.name
      this.nameEN = routeData.nameEN
      this.positionid = routeData.positionid
      this.instrumentid = routeData.instrumentid
      this.digits = routeData.digits
      this.direction = routeData.direction
      this.rate = routeData.rate
      this.priceperpoint = routeData.priceperpoint
      this.tick = routeData.tick
      let tickCopy = JSON.parse(localStorage.getItem('tickCopy'))
      let tickCopyData = tickCopy[this.instrumentid]
      console.log(tickCopy[this.instrumentid])
      this.newClose = (tickCopyData.price).toFixed(this.digits)
      this.stepperTwo = this.newClose
      this.bidPrice = (tickCopyData.bidprice).toFixed(this.digits)
      this.askPrice = (tickCopyData.askprice).toFixed(this.digits)
      this.add = (tickCopyData.price-tickCopyData.preclose).toFixed(this.digits)
      if(tickCopyData.preclose === 0){
        this.diffPriceRate = '--'
      }else {
        this.diffPriceRate = (((Number(tickCopyData.price)-Number(tickCopyData.preclose))/tickCopyData.preclose)*100).toFixed(this.digits)+'%';
      }
      this.handdleMsg()
    },
    methods:{
      // 弹出框-卖出
      sellOut () {
        this.showClosePosition = true
        this.bondClose = (this.$route.params.bondClose*this.stepperOne).toFixed(2)
        this.serviceChargeClose = this.$route.params.serviceChargeClose*this.stepperOne
      },
      // 弹出框-平仓
      closePosition () {
        this.showSellOut = true
        this.bondClose = (this.$route.params.bondClose*this.stepperOne).toFixed(2)
        this.serviceChargeClose = this.$route.params.serviceChargeClose*this.stepperOne
      },
      quickOrder(a){
        this.butTab = a
        if(a === 0){
          this.stopNum = true
          this.stepperTwo = this.newClose
          this.priceType = 1
          this.FixedPriceOff = true
          console.log('下市价单')
        }else {
          this.stopNum = false
          this.stepperTwo = this.newClose
          this.priceType = 0
          this.FixedPriceOff = false
          console.log('下限价单')
        }
      },
      // 关闭上拉框
      noShowSheet () {
        this.$router.push({
          name:'trade',
        })
      },
      // 提交订单---买
      ConfirmClosing(){
        let that = this
        if (that.global.ws && that.global.ws.readyState == 1) {
          this.global.ws.onopen = this.setPlaceOrder()
        }
        that.global.ws.onmessage = function(res) {
          that.getPlaceOrder (res)
        };
      },
      setPlaceOrder(){
        let actions;
        let that = this;
        actions = {
          "action":"placeorder",
          "reqID":103,
          "data":{
            "id":null,
            "userid":null,
            "settlementid":null,
            "source":null,
            "type":0,
            "positionid":null,
            "instrumentid":that.instrumentid,
            "orderRef":"b321ecab-d67d-450c-b2bb-b8caa233877d",
            "ordersysid":null,
            "direction":0,
            "volume":that.stepperOne,
            "volumeTraded":null,
            "priceType":that.priceType,
            "limitPrice":that.stepperTwo,
            "distanceStopPrice":null,
            "distanceProfitPrice":null,
            "status":null,
            "closed":null,
            "frozenMargin":null,
            "frozenMarginMember":null,
            "tradingDay":null,
            "createtime":null,
            "updatetime":null,
            "canceltime":null,
            "hedge":null,
            "hedgeChannel":null,
            "distanceStop":0.0,
            "distanceProfit":0.0
          }};
        // console.log(JSON.stringify(actions));
        that.global.ws.send(JSON.stringify(actions));
      },
      getPlaceOrder(e){
        let that = this
        this.redata = JSON.parse(e.data)
        console.log(this.redata)
        if(this.redata.action === "placeorder"){
          if(this.redata.data.code === 0){
            Toast({
              message: that.$t('addMessage'),
              duration: 500,
              position:'bottom',
              onClose() {
                that.$router.push({
                  name:'blank'
                })
              }
            });
          }else {
            Toast({
              message: that.redata.data.msg,
              duration: 500,
              position:'bottom',
              onClose() {
                that.$router.push({
                  name:'blank'
                })
              }
            });
          }
        }

      },
      CancelClosing(){
        let that = this
        if (that.global.ws && that.global.ws.readyState == 1) {
          this.global.ws.onopen = this.setPlaceOrder2()
        }
        that.global.ws.onmessage = function(res) {
          that.getPlaceOrder2 (res)
        };
      },
      setPlaceOrder2(){
        let actions;
        let that = this;
        actions = {
          "action":"placeorder",
          "reqID":104,
          "data":{
            "id":null,
            "userid":null,
            "settlementid":null,
            "source":null,
            "type":0,
            "positionid":null,
            "instrumentid":that.instrumentid,
            "orderRef":"b321ecab-d67d-450c-b2bb-b8caa233877d",
            "ordersysid":null,
            "direction":1,
            "volume":that.stepperOne,
            "volumeTraded":null,
            "priceType":that.priceType,
            "limitPrice":that.stepperTwo,
            "distanceStopPrice":null,
            "distanceProfitPrice":null,
            "status":null,
            "closed":null,
            "frozenMargin":null,
            "frozenMarginMember":null,
            "tradingDay":null,
            "createtime":null,
            "updatetime":null,
            "canceltime":null,
            "hedge":null,
            "hedgeChannel":null,
            "distanceStop":0.0,
            "distanceProfit":0.0
          }};
        that.global.ws.send(JSON.stringify(actions));
      },
      getPlaceOrder2(e){
        let that = this
        this.redata = JSON.parse(e.data)
        console.log(this.redata)
        if(this.redata.action === "placeorder"){
          if(this.redata.data.code === 0){
            Toast({
              message: '追加下单成功！',
              duration: 1000,
              position:'bottom',
              onClose() {
                that.$router.push({
                  name:'blank'
                })
              }
            });
          }else {
            Toast.fail({
              message: this.redata.data.msg,
              duration: 1000,
              position:'bottom',
              onClose() {
                that.$router.push({
                  name:'blank'
                })
              }
            });
          }
        }
      },
      // 获取余额
      handdleMsg(msg) {
        let that = this;
        if (that.global.ws && that.global.ws.readyState == 1) {
          that.balanceTimeData()
        }
        that.global.ws.onmessage = function(res) {
          that.websocketonmessage (res)
        };
      },
      balanceTimeData(){
        this.global.ws.onopen = this.onopen()
      },
      onopen () { // websocket连接后发送数据(send发送)
        let actions;
        let that = this
        actions = {
          action: "loadusermoney",
          reqID: 17,
        }
        that.global.ws.send(JSON.stringify(actions))
      },
      websocketonerror () { // 连接建立失败重连
        this.getLogin()
      },
      websocketonmessage (e) { // 数据接收
        this.redata = JSON.parse(e.data)
        console.log(this.redata)
        if(this.redata.data.code === 0 && this.redata.action === "loadusermoney"){
          this.balance = this.redata.data.object.balance
        }
      },
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/trade";
</style>



